import { Box, Container, Grid, Paper, Typography, Icon, IconButton } from '@material-ui/core';
import React, { useState, useEffect, useCallback } from 'react';
import Button from '@material-ui/core/Button';
import bgimg from './mainbg.jpg';
import srch from './searchicon.svg';
import pin from './pincodeicon.svg';
import briefcase from './briefcase.svg';
import ContactIcon from './contact.svg';
import ContactHoverIcon from './contacthover.svg';
import DollarSign from './dollarsign.svg';
import EyesImg from '../../assets/eyes.png';
import ClicksImg from '../../assets/clicks.png';
import SchoolLogo from './schoollogo.svg';
import SmolBag from '../../assets/smolBag.png';
import HeartIcon from './heart.svg';
import HeartOnIcon from './hearton.svg';
import ProfileImg1 from './demoprofile1.svg';
import ProfileImg2 from './demoprofile2.svg';
import ProfileImg3 from './demoprofile3.svg';
import ProfileImg4 from './demoprofile4.svg';
import Clock from './clock.svg';
import Phd from './phd.svg';
import './mydemo.css';
import Teacher from './teacher.svg';
import FullTime from './fullTime.svg';

const styles = {
    mainBG: {
        background: `url(${bgimg}), #c5c8cc`,
        backgroundSize: 'contain',
        backgroundRepeat: 'no-repeat',
        backgroundPosition: 'center',
        minHeight: '80vh',
        paddingTop: '50vh',
        paddingLeft: '10vh',
        paddingRight: '10vh',
    },
    postedJob: {
        paddingLeft: '10vh',
        paddingRight: '10vh',
    },
    cardstyle: {
        background: '#fff',
        borderRadius: '12px',
        padding: '5vh',
    },
    searchBox: {
        fontSize: '14px',
        fontFamily: 'Montserrat',
        fontWeight: '600',
        background: '#fff',
        border: '1px solid #BEBEBE',
        borderRadius: '25px',
        paddingLeft: '4vh',
        paddingRight: '4vh',
        paddingTop: '10px',
        paddingBottom: '10px',
    },
    srchField: {
        border: "none",
        borderColor: "transparent",
        outline: "none",
        width: "25vw",
    },
};

const Stats = ( props ) => {
    return (
      <div className="statsBadge">
        <img src={ props.img } alt="" />
        <div>
          <div className="stat">{props.stat}</div>
          <div className="statName">{props.statName}</div>
        </div>
      </div>
    )
}

const ProfileTags = (props) => {
    return (
      <div className={`profileTag ${props.img ? null : 'tagBoldText' }`} style={{backgroundColor: '#fff'}}>
        <img src={props.img} alt="" />
        {props.text}
      </div>
    )
}

const SalaryTag = (props) => {
    return (
      <div className={`profileTag`} style={{backgroundColor: '#fff'}}>
        {[...Array(parseInt(props.n))].map((el, index) => <img src={DollarSign} alt="$" />)}
        <img src={props.img} alt="" />
        Salary Estimate
      </div>
    )
}

const PostedJobCard = (props) => {
    return(
        <div className="postedJobCard">
            <div className="postedTitle">{props.title}</div>
            <div className="postedSubtitle">{props.expiry}</div>
            <br/>
            <div className="postedTags">
                <SalaryTag n='4'/>
                <ProfileTags img={Clock} text='8 Hours Shift' />
                <ProfileTags img={SmolBag} text='Full-time, Part-time' />
                <ProfileTags img={Phd} text='PHD Preferred' />
            </div>
        </div>
        );
}
const JobCard = (props) => {
    return (
      <div className="SearchJobCard">
            <Grid container direction='row' spacing={4}>
                <Grid item xs={6}>
                    <div className="cardStatsCont">
                        <Stats img={ briefcase } stat={ props.jobsposted } statName="Jobs Posted" />
                        <Stats img={ EyesImg } stat={ props.views } statName="Job Views" />
                        <Stats img={ ClicksImg } stat={ props.applied } statName="Clicked Apply" />
                    </div>
                    <br/>
                    <br/>
                    <div className="postedJobs">
                        <p style={{fontFamily: 'Montserrat', fontWeight: 700, fontSize: 19 }}>Jobs Posted <a href="#viewall" style={{fontFamily: 'Montserrat', fontWeight: 400, fontSize: 14, paddingLeft: '10px', textDecoration: 'none', color: '#000'}}>view all &gt;</a></p>
                        <PostedJobCard title={props.title} expiry="Job expiry : 11/08/21 (7 days left apply now)" />
                    </div>
                </Grid>
                <Grid item xs={6}>
                    <div className="cardContainer">
                        <div className="cardTitleCont">
                            <div className="cardHeader">
                                <img src={props.schoollogo} alt={props.school}/>
                                <div className="cardActions">
                                    <button className='headerBtn' style={{ color: '#0667C3', fontWeight: '500', padding: '0.5em 1.5em', fontSize: '14px', border: '1px solid #0667C3', borderRadius: '20px', background: '#fff' }}>Edit</button> 
                                    <button className='headerBtn' style={{ color: '#0667C3', fontWeight: '500', padding: '0.5em 1.5em', fontSize: '14px', border: '1px solid #0667C3', borderRadius: '20px', background: '#fff' }}>Preview</button> 
                                </div>
                            </div>
                            <br/>
                            <div className="cardTitleBlock">
                                <div className="cardTitle">{props.school}</div>
                                <br/>
                                <div className="cardSchool">{props.address}</div>
                            </div>
                            <br/>
                        </div>
                        <div className="aboutInfo">
                            <div className="aboutTitle">About us</div>
                            <div className="aboutDesc">{props.about}</div>
                        </div>
                    </div>
                </Grid>
            </Grid>
        </div>
    )
}

const FeaturedData = [
    {
        profile: ProfileImg1,
        title: 'Krishna Barbe',
        location: 'San Francisco, CA',
    },
    {
        profile: ProfileImg2,
        title: 'Annabel Rohan',
        location: 'San Francisco, CA',
    },
    {
        profile: ProfileImg3,
        title: 'Chieko Chute',
        location: 'San Francisco, CA',
    },
];

const RecommendedData = [
    {
        profile: ProfileImg1,
        title: 'Krishna Barbe',
        location: 'San Francisco, CA',
    },
    {
        profile: ProfileImg2,
        title: 'Annabel Rohan',
        location: 'San Francisco, CA',
    },
    {
        profile: ProfileImg3,
        title: 'Chieko Chute',
        location: 'San Francisco, CA',
    },
    {
        profile: ProfileImg4,
        title: 'Tynisha Obey',
        location: 'San Francisco, CA',
    }
];


const FeaturedCandidate = (props) => {
    return (
        <div className="featuredCandidate">
            <Grid container direction='column'>
                <Grid item xs={12}>
                    <Grid container direction='row' spacing={4}>
                        <Grid item xs={3}>
                            <img src={props.profile} alt={props.title}/>
                        </Grid>
                        <Grid item xs={6}>
                            <span style={{ fontWeight: '600' }}>{props.title}</span>
                            <br />
                            <span style={{ fontSize: '12px' }}>{props.location}</span>
                        </Grid>
                        <Grid direction='row' justifyContent='flex-end' container item xs={3}>
                            <button className='heartButton' style={{ border: 'none', background: '#fff' }} onClick={() => {
                                var curEl = document.getElementById('heart' + props.id);
                                if (curEl.getAttribute('src') === HeartIcon) {
                                    curEl.setAttribute('src', HeartOnIcon);
                                } else {
                                    curEl.setAttribute('src', HeartIcon);
                                }
                            }}>
                                    <img src={HeartIcon} alt="Favourite" style={{width: '100%', height: '100%'}} id={'heart'+props.id}/>
                            </button>
                        </Grid>
                    </Grid>
                </Grid>
                <br/>
                <Grid item xs={12}>
                    <div className='postedTags'>
                        <div style={{ border: '1px solid #E4E8F1', margin: '5px', background: '#FFF', height: '34px', padding: '0 10px', borderRadius: '25px', display: 'flex', alignItems: 'center' }}    >
                            <img src={Teacher} alt=""/>
                            <button style={{ background: '#FFF', border: 'none', fontSize: '12px' }} >Teachers license</button>
                        </div>
                        <div style={{ border: '1px solid #E4E8F1', margin: '5px', background: '#FFF', height: '34px', padding: '0 10px', borderRadius: '25px', display: 'flex', alignItems: 'center' }}    >
                            <img src={FullTime} alt=""/>
                            <button style={{ background: '#FFF', border: 'none', fontSize: '12px' }} >5 Year Work Experience</button>
                        </div>
                        <div style={{ border: '1px solid #E4E8F1', margin: '5px', background: '#FFF', height: '34px', padding: '0 10px', borderRadius: '25px', display: 'flex', alignItems: 'center' }}    >
                            <img src={Phd} alt=""/>
                            <button style={{ background: '#FFF', border: 'none', fontSize: '12px' }} >Phd completed</button>
                        </div>
                        <div style={{ border: '1px solid #E4E8F1', margin: '5px', background: '#FFF', height: '34px', padding: '0 10px', borderRadius: '25px', display: 'flex', alignItems: 'center' }}    >
                            <img src={FullTime} alt=""/>
                            <button style={{ background: '#FFF', border: 'none', fontSize: '12px' }} >Full Time, Part Time</button>
                        </div>
                    </div>
                </Grid>
                <Grid item xs={12}>
                    <div className="contactDiv" onMouseEnter={() => {
                        var curElem = document.getElementById("contactbtn" + props.id);
                        curElem.setAttribute('src', ContactHoverIcon);
                    }} onMouseLeave={() => {
                        var curElem = document.getElementById("contactbtn" + props.id);
                        curElem.setAttribute('src', ContactIcon);
                    }}>
                        <button className="contact"><img src={ContactIcon} alt="" id={"contactbtn" + props.id}/>   Contact</button>
                    </div>
                </Grid>
            </Grid>
        </div>
    );
}

const RecommendedCandidate = (props) => {
    return (
        <div className="recommendedCandidate">
            <Grid container direction='column'>
                <Grid item xs={12}>
                    <Grid container direction='row' spacing={4}>
                        <Grid item xs={3}>
                            <img src={props.profile} alt={props.title}/>
                        </Grid>
                        <Grid item xs={6}>
                            <span style={{ fontWeight: '600' }}>{props.title}</span>
                            <br />
                            <span style={{ fontSize: '12px' }}>{props.location}</span>
                        </Grid>
                        <Grid direction='row' justifyContent='flex-end' container item xs={3}>
                            <button className='heartButton' style={{ border: 'none', background: '#fff' }} onClick={() => {}}>
                                    <img src={HeartIcon} alt="Favourite" style={{width: '100%', height: '100%'}} id={'heart'+props.id}/>
                            </button>
                        </Grid>
                    </Grid>
                </Grid>
                <br/>
                <Grid item xs={12}>
                    <Grid container direction='row' spacing={4}>
                        <Grid item xs={10}>
                            <div className='postedTags'>
                                <div style={{ border: '1px solid #E4E8F1', margin: '5px', background: '#FFF', height: '34px', padding: '0 10px', borderRadius: '25px', display: 'flex', alignItems: 'center' }}    >
                                    <img src={Teacher} alt=""/>
                                    <button style={{ background: '#FFF', border: 'none', fontSize: '12px' }} >Teachers license</button>
                                </div>
                                <div style={{ border: '1px solid #E4E8F1', margin: '5px', background: '#FFF', height: '34px', padding: '0 10px', borderRadius: '25px', display: 'flex', alignItems: 'center' }}    >
                                    <img src={FullTime} alt=""/>
                                    <button style={{ background: '#FFF', border: 'none', fontSize: '12px' }} >5 Year Work Experience</button>
                                </div>
                                <div style={{ border: '1px solid #E4E8F1', margin: '5px', background: '#FFF', height: '34px', padding: '0 10px', borderRadius: '25px', display: 'flex', alignItems: 'center' }}    >
                                    <img src={Phd} alt=""/>
                                    <button style={{ background: '#FFF', border: 'none', fontSize: '12px' }} >Phd completed</button>
                                </div>
                                <div style={{ border: '1px solid #E4E8F1', margin: '5px', background: '#FFF', height: '34px', padding: '0 10px', borderRadius: '25px', display: 'flex', alignItems: 'center' }}    >
                                    <img src={FullTime} alt=""/>
                                    <button style={{ background: '#FFF', border: 'none', fontSize: '12px' }} >Full Time, Part Time</button>
                                </div>
                            </div>
                        </Grid>
                        <Grid item xs={2}>
                            <div className="contactDiv" style={{paddingTop: '3em'}}>
                                <button className="contact" style={{paddingLeft: '1em', paddingRight: '1em'}}>Contact</button>
                            </div>
                        </Grid>
                    </Grid>
                </Grid>
            </Grid>
        </div>
    );
}



const FeaturedCandidates = (props) => {
    return (
        <div className="featuredCandidates">
            <Typography style={{ fontSize: '28px', fontStyle: "Work Sans", fontWeight: '600', color: '#000', marginBottom: '20px', marginTop: '40px' }}>Featured Candidates</Typography>
            <Grid container direction='row' spacing={3} justifyContent='center'>
                {FeaturedData.map((candidate, i) => {
                    return(
                        <Grid item xs={4}>
                            <FeaturedCandidate profile={candidate.profile} title={candidate.title} location={candidate.location} id={i} />
                        </Grid>
                    );
                })}
            </Grid>
        </div>
    );
}

const RecommendedCandidates = (props) => {
    return (
        <div className="recommendedCandidates">
            <Typography style={{ fontSize: '28px', fontStyle: "Work Sans", fontWeight: '600', color: '#000', marginBottom: '20px', marginTop: '40px' }}>Recommended Candidates</Typography>
            <Grid container direction='row' spacing={3} justifyContent='center'>
                {RecommendedData.map((candidate, i) => {
                    return(
                        <Grid item xs={6}>
                            <RecommendedCandidate profile={candidate.profile} title={candidate.title} location={candidate.location} id={i} />
                        </Grid>
                    );
                })}
            </Grid>
        </div>
    );
}


function myDemoPage(){
    return(
        <div classname='mainContainer'>
            <div classname="bgimg" style={styles.mainBG}>
                <Paper style={styles.cardstyle} elevation={0}>
                    <Grid container spacing={3}>
                        <Grid item xs={12}>
                            <Typography style={{fontSize: '23', fontFamily: 'Montserrat', fontWeight: '600'}}>Candidate Search</Typography>
                        </Grid>
                        <Grid item xs={10}>
                            <div className="searchBox" style={styles.searchBox}>
                                <Grid container direction='row' spacing={1}>
                                    <Grid item xs={6}>
                                        <Grid container direction='row'>
                                            <Grid item xs={1}>
                                                <img src={srch} alt="Search by Title"/>
                                            </Grid>
                                            <Grid item xs={11}>
                                                <input type="text" placeholder="Search Candidate by Job Title" style={styles.srchField} />
                                            </Grid>
                                        </Grid>
                                    </Grid>
                                    <Grid item xs={6}>
                                        <Grid container direction='row'>
                                            <Grid item xs={1}>
                                                <img src={pin} alt="Search by Location"/>
                                            </Grid>
                                            <Grid item xs={5}>
                                                <input type="text" placeholder="Location or Zip Code" style={styles.srchField} />
                                            </Grid>
                                        </Grid>
                                    </Grid>
                                </Grid>
                            </div>
                        </Grid>
                        <Grid item xs={2}>
                        <Button id='srchButton' style={{ textTransform: 'none', fontSize: "14px", fontFamily: 'Montserrat', background: '#EB881B', color: '#FFF', fontWeight: '600', borderRadius: '25px', padding: '10px', minWidth: '100%' }} variant="contained" >Search</Button>
                        </Grid>
                    </Grid>
                </Paper>
            </div>
            <br/>
            <div className="mainContent">
                <div className="postedJob" style={styles.postedJob}>
                    <JobCard title='High School English Teacher - Leave Replacement Teacher' school='Woodside International School of San Francisco' expire='6/20/2021' jobsposted='2' views='150' applied='130' address="1555 Irving St, San Francisco, CA 94122, United States" about="Lorem ipsum dolor sit amet, consectetur adipiscing elit. Felis, sit tempus fermentum orci aenean ut pulvinar facilisis volutpat. Consectetur lorem semper ut amet, diam interdum. Lectus ac velit viverra vestibulum. Non duis magna arcu, diam, id. Vel nunc nulla posuere nisl sit ut. Diam nulla sed dictum volutpat. Lacus, commodo pulvinar eu aliquet nunc. Urna ac iaculis fusce." schoollogo={SchoolLogo}/>
                </div>
                <br/>
                <div className="featured">
                    <FeaturedCandidates />
                </div>
                <br/>
                <div className="recommended">
                    <RecommendedCandidates />
                </div>
            </div>
        </div>
    ); 
}

export default myDemoPage;