function getWindowDimensions() {
    const { innerWidth: width, innerHeight: height } = window;
    return {
        width,
        height
    };
}
function useWindowDimensions() {
    const [windowDimensions, setWindowDimensions] = useState(
        getWindowDimensions()
    );

    useEffect(() => {
        function handleResize() {
            setWindowDimensions(getWindowDimensions());
        }

        window.addEventListener("resize", handleResize);
        return () => window.removeEventListener("resize", handleResize);
    }, []);

    return windowDimensions;
}

const EmployerDashboard = () => {
  const [value, setValue] = useState( 'acp' )
  const [createJobPost, setCreateJobPost] = useState(false)
  
  const handleChange = (event, newValue) => {
        setValue(newValue)
    }

  const acp = <div className='EmpDashCont'>
    <div className="activeStats">
      <div className="cardStatsCont">
        <Stats img={ MessagesImg } stat='150' statName="Messages" />
        <Stats img={ EyesImg } stat='150' statName="Job Views" />
        <Stats img={ ClicksImg } stat='150' statName="Clicked Apply" />
      </div>
      <button className="cardDraft" onClick={() => setCreateJobPost(true)}>Create job post</button>
    </div>
    <JobCard title='High School English Teacher - Leave Replacement Teacher' school='Summit Public Schools' postedAgo='23 days ago' expire='6/20/2021' messages='150' views='150' applied='150' />
  </div>

  const draft = <div className='EmpDashCont'>
    <JobCard title='High School English Teacher - Leave Replacement Teacher' school='Summit Public Schools' postedAgo='23 days ago' expire='6/20/2021' draft />
  </div>
  
  const renderSwitch = (param) => {
    switch (param) {
      case "acp":
          return acp;
      case "draft":
        return draft;
    }
  }

  return (
    <div style={ { background: '#ffffff' } }>
      <Helmet>
                <title>Frame Change</title>
            </Helmet>
      <NavBar />
            <Container style={{marginTop: '10vh'}}>

                <div style={{ paddingTop: '2em' }}>
                    <div style={{ width: '100%' }}>

                        <Tabs
                            variant="scrollable"
                            scrollButtons
                            allowScrollButtonsMobile
                            value={value}
                            onChange={handleChange}
                            textColor="inherit"
                            TabIndicatorProps={{ style: { background: 'orange' } }}
                            aria-label="secondary tabs example"
                        >
                            <Tab style={{ textTransform: 'capitalize' }} value="acp" label="Active job posts" />
                            <Tab style={{ textTransform: 'capitalize' }} value="draft" label="Draft" />
                        </Tabs>
                    </div>
                    {renderSwitch(value)}
                </div>
      </Container>
      <Modal
        open={createJobPost}
        onClose={() => setCreateJobPost(false)}
        aria-labelledby="modal-modal-title"
        aria-describedby="modal-modal-description"
      >
        <div className='modalCont'><JobPostForm closeModal={() => setCreateJobPost(false)} /></div>
      </Modal>
        </div>
  )
}

export default EmployerDashboard

