const deleteFunc = (e) => {
  // console.log(e.target)
  let target = e.target.id.split('-');
  const id = target.at(-1);
  target = target[0];
  // console.log(target, id);

  axiosInstance.delete(`/seeker/${target}/`, {
    params : {
      "access" : accessToken,
      "id" : parseInt(id)
    }
  }).then(res=>{
    axiosInstance.get(`/seeker/${target}/`, {
      params:{
        "access" : accessToken
      }
    }).then(res=>{
      alert('Successfully Deleted');
      getData();
    }).catch(err=>{
      alert(err);
    })
  }).catch(err=>{
    alert(err);
  })
}


